.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.app {
  height: 100%;
}

.app .container {
  max-width: 100%;
  padding-left: 0;
  padding-right: 0;
  height: 100%;
}

.app .container .row {
  height: 100%;
}

.left-col {
  display: flex;
  align-items: center;
}

.right-col {
  max-height: 100%;
  overflow: scroll;
  background: #efefef;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
